
import React, { useState, useEffect } from 'react';

import axios from 'axios';

import ArticlesList from '../components/articlesList';

import CommentsList from '../components/commentsList';

import UpvotesSection from '../components/upVotesSection';

import AddCommentForm from '../components/addCommentForm';

import NotFoundPage from './notFoundPage';

import articleContent from './article-content';

import UseUser from '../hooks/useUser';

import { useHistory } from 'react-router-dom';

const ArticlePage = ({match}) => {

    const [articleInfo, setArticleInfo] = useState({ upvotes: 0, comments: [], canUpvote:false, });

    const articleId = match.params.name;

    const { user, isLoading } = UseUser();

    useEffect(() => {

        const LoadArticleInfo = async () => {
            const token = user && await user.getIdToken();
            const headers = token ? { authtoken: token } : {};
            const response = await axios.get(`/api/articles/${articleId}`,{ headers });
            const newArticleInfo = response.data;
            setArticleInfo(newArticleInfo);
        }
        if(!isLoading){
           LoadArticleInfo();    
        }
        
    }, [ isLoading, user ]);

    const article = articleContent.find(article => article.name === articleId);

    const { canUpvote, upvotes,comments } = articleInfo;

    const addUpvote = async() => {
            const token = user && await user.getIdToken();
            const headers = token ? { authtoken: token } : {};
            const response = await axios.post(`/api/articles/${articleId}/upvote`,null, { headers });
            const updatedArticle = response.data;
            setArticleInfo(updatedArticle);
    }
    if (!article) return <NotFoundPage />
    const otherArticles = articleContent.filter(article => article.name !== articleId);
    return (
       <> 
        <div className="page-container">
           <div className="content-container">
        <h1>{article.title}</h1>
         <div id="upvotes-section">
         { user ?
            <button disabled={!canUpvote} onClick={()=>addUpvote()}>{canUpvote ? "upvote" :"Already upvoted"}</button>   
             :  
            <button onClick={null}>Login to upvote</button>
         }   
            <div id="add-comment-form">
             <p>This article has {articleInfo.upvotes} upvote(s)</p>
        </div>     
            </div> 
        {article.content.map((paragraph, key) => (
            <p key={key}>{paragraph}</p>
        ))}
    
        <CommentsList comments={articleInfo.comments} />
        { user ?
        <AddCommentForm articleName={articleId} onArticleUpdated={setArticleInfo} />
          :
          <button>Login to add Comment</button>   
        }  
        <h3>Other Articles:</h3>
        <ArticlesList articles={otherArticles} />
            </div>
        </div>  
     </>     
    );
}

export default ArticlePage;